import { Injectable } from '@angular/core';
import {APIService} from "./API.service";


@Injectable()
export class KivalasztottIngatlanokService {
  kivalasztottIngatlanok:any[]=[];
  constructor(
      private _APIService:APIService

  ) {
    this.loadkivalasztottInfatlanok();
  }
  loadkivalasztottInfatlanok(){
    // let q=(this.listView)?'api/ingatlans/where=id>1':'api/ingatlans/where=id>1&offset='+this.offset+'&limit='+this.limit;
    let q='api/ingatlans/kivalasztott';
    this._APIService.getResponseGET(q,'').subscribe(
            data=> {
          console.log(data);
          this.kivalasztottIngatlanok=data;


        }
        ,
            error=> {
        console.log(error);
        }
    )
  }
  public setKuvalasztottIngatlanok(ingatlanok){

    this.kivalasztottIngatlanok=[];

    this.kivalasztottIngatlanok=ingatlanok;
  }
}
